.section-projet {
	margin-top: 5em;
}

.section-projet h1 {
	font-size: 3em;
	text-align: center;
}

.section-projet h1 + p {
	color: var(--purple-light);
	font-size: 2em;
	text-align: center;
}

.projet-list {
	margin-top: 3em;
}

.projet {
	margin: 4.5em;
}

.projet h1 {
	font-size: 2em;
}

.projet img {
	display: block;
	width: 100%;
	height: 100%;
	transition: 0.5s;
}

.projet-content {
	/* border: 1px solid blue; */
	display: flex;
	position: relative;
	margin-top: 2em;
	gap: 2em;
}


.projet-content a:first-child {
	/* border: 2px solid orange; */
	display: block;
	width: 800px;
	height: 500px;
	position: relative;
}

.projet-content a p {
	box-sizing: border-box;
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	margin: 0;
	padding-top: 250px;
	text-align: center;
	color: white;
	font-size: 4em;
	background-color: rgba(161, 144, 228, 0.647);
	opacity: 0%;
	transition: 1s;
}

.projet-description {
	width: 35em;
	font-size: 1.5em;
	line-height: 1.6em;
}