.section-projet {
	margin-top: 5em;
}

.section-projet h1 {
	font-size: 3em;
	text-align: center;
}

.section-projet h1 + p {
	color: var(--purple-light);
	font-size: 2em;
	text-align: center;
}

.projet-list {
	margin-top: 3em;
}

.projet {
	margin: 4.5em;
}

.projet h1 {
	font-size: 2em;
}

.projet img {
	display: block;
	width: 100%;
	height: 100%;
	transition: 0.5s;
}

.projet-content {
	/* border: 1px solid blue; */
	display: flex;
	position: relative;
	margin-top: 2em;
	grid-gap: 2em;
	gap: 2em;
}


.projet-content a:first-child {
	/* border: 2px solid orange; */
	display: block;
	width: 800px;
	height: 500px;
	position: relative;
}

.projet-content a p {
	box-sizing: border-box;
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	margin: 0;
	padding-top: 250px;
	text-align: center;
	color: white;
	font-size: 4em;
	background-color: rgba(161, 144, 228, 0.647);
	opacity: 0%;
	transition: 1s;
}

.projet-description {
	width: 35em;
	font-size: 1.5em;
	line-height: 1.6em;
}
:root {
	--grey: rgb(199, 199, 199);
	--grey-dark: #242424;
	--purple: #8d3fbd;
	--purple-light: #9c9cdd;
}

body {
	position: absolute;
	width: 100%;
	height: 100%;
	margin: 0;
	background-color: #242424;
	background-color: var(--grey-dark);
	color: rgb(199, 199, 199);
	color: var(--grey);
	font-family: 'Raleway', sans-serif;
}

h1 {
	color: #9c9cdd;
	color: var(--purple-light);
	margin: 0px;
}

h2 {
	/* color: var(--purple-light); */
	/* font-size: 3em; */
	margin: 0;
	margin-bottom: 2em;
	font-style: italic;
}

a {
	color: unset;
	text-decoration: unset;
}

a:visited, a:link {
	color: unset;
}

.section-main {
	width: 24em;
	height: calc(100vh - 1em);
	position: relative;
	margin: auto;
	margin-top: 1em;
	margin-bottom: 0;
	font-size: 2em;
}

.section-main h1,
a[href="#projet-list"]
{
	color: #9c9cdd;
	color: var(--purple-light);
	margin: 0px;
}

.section-main img {
	float: left;
	border-radius: 400px;
	width: 5em;
	margin-right: 1.5em;
}

.section-main p {
	text-align: justify;
	line-height: 1.5em;
}

.section-main div {
	position: absolute;
	width: 100%;
	bottom: 0;
	text-align: center;
	color: #9c9cdd;
	color: var(--purple-light);
}

.section-main div p,
a[href="#projet-list"]
{
	text-align: center;
}

nav {
	width: 9em;
	position: absolute;
	left: 1.5em;
	font-size: 1.3em;
}

nav .button:first-child {
	background-color: #8d3fbd;
	background-color: var(--purple);
}

.button {
	display: block;
	margin: 1em;
	font-family: 'Raleway', sans-serif;
	color: white;
	text-align: center;
	border: 0;
	border-radius: 22px;
	padding: 39px;
	padding: 0.5em 1.4em;

	background-color: #5050b6;
	transition: background-color 500ms;
}

.button:hover, nav .button:first-child:hover {
	background-color: #9c9cdd;
	background-color: var(--purple-light);
	transition-duration: 500ms; 
}

nav a img {
	width: 100%;
}
